import React, { useState, useEffect } from "react";
import Login from "./Login";
import {
	BrowserRouter as Router,
	Routes,
	Route,
	Link
} from 'react-router-dom';
import Homepage from "./Homepage";
import Profile from "./pages/Profile";
import MySubGreddiits from "./pages/MySubGreddiits";
import SubGreddiits from "./pages/SubGreddiits";
import Saved from "./pages/Saved"
import { ThemeProvider, createTheme } from '@mui/material/styles';
const theme = createTheme({
  palette: {
    primary: {
      main: '#ec2323',
    },
    secondary: {
      main: '#23ecec',
    },
  }
});
function App() {
  const loggedin = localStorage.getItem("isLoggedIn");
  return (
    <div>
    <ThemeProvider theme={theme}>
    </ThemeProvider>
    <Router>
      <Routes>
        <Route exact path='/' element={loggedin ? <Homepage /> : <Login />} ></Route>
        <Route exact path='/homepage' element={< Homepage />}></Route>
        <Route exact path="/homepage/profile" element={<Profile />}></Route>
        <Route exact path="/homepage/mysub" element={<MySubGreddiits />}></Route>
        <Route exact path="/homepage/sub" element={<SubGreddiits />}></Route>
        <Route exact path="/homepage/saved" element={<Saved />}></Route>
      </Routes>
    </Router>
    </div>
    // <div>
    //   <Login />
    // </div> 
  );
}

export default App;
