const followers = ['A','B','C'];
const following = ['D','E','F','G'];
function BasicMenu({props, name}) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const [people, newPeople] = useState(props);
  const handleDelete= (index) => (event) => {
    event.preventDefault();
    newPeople([
      ...people.slice(0, index),
      ...people.slice(index + 1,people.length)
    ]);
  }
  return (
    <div>
      <h4 align="center">{name}</h4>
      <Button
        id="basic-button"
        aria-controls={open ? 'basic-menu' : undefined}
        aria-haspopup="true"
        aria-expanded={open ? 'true' : undefined}
        onClick={handleClick}
        sx={{width: '180px', fontSize: '40px'}}
      >
        {people.length}
      </Button>
      <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          'aria-labelledby': 'basic-button',
        }}
      >
        {people.map((item, index) =>(
          <MenuItem onClick={handleClose}>{item}
          <IconButton aria-label="delete" onClick={handleDelete(index)}>
          <DeleteIcon />
          </IconButton> 
          </MenuItem>
        ))}
      </Menu>
    </div>
  );
}
